body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    /* background-color:black; */
}
.App{
    text-align: left;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 20px;
}
.footer{
    width: 100vw;
    background-color:navy;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.footer:hover{
    width: 100vw;
    background-color:yellowgreen;
    color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.minus,.plus{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color:yellow;
    color: black;
    font-size: 20px;
}
.Buttons{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    /* align-items: center; */

}
.container-fluid{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    height: 100vh;
}
